import * as React from 'react'
import * as ReactDOM from 'react-dom'
import { useState, useContext, createContext } from 'react'
import axios from 'axios'

type form =  any
const FormContext = createContext<form>([])
const ChangeTextContext = createContext<form>([])

const FormComponent = () => {
    const [name, email, memo, address, job, height] = useContext(FormContext)
    const [setName, setEmail, setJob, setAddress, setMemo, setHeight, setBirthday] = useContext(ChangeTextContext)
    
    return (
        <form name="forms">    
            <input type="text" name={name} value={name} onChange={(e:any) => setName(e.target.value)} placeholder="名前"　/><br />
            <input type="text" name={email} value={email} onChange={(e:any) => setEmail(e.target.value)} placeholder="メールアドレス" /><br />
            <label htmlFor="calendar">生年月日:</label><br />
            <input type="date" max="9999-12-31"  onChange={(e:any) => setBirthday(e.target.value)} /><br />
            <input type="text" value={job} onChange={(e:any) => setJob(e.target.value)} placeholder="職業" /><br />
            <input type="text" name={address} value={address} onChange={(e:any) => setAddress(e.target.value)} placeholder="住所" /><br />
            <input type="text" name={height} value={height} onChange={(e:any) => setHeight(e.target.value)} placeholder="身長"/><br />
            <CheckComponent />   
            <textarea name={memo} value={memo} onChange={(e:any) => setMemo(e.target.value)} placeholder="メモ"></textarea><br /> 
            <ButtonCompoent />
        </form>
    )
}

const CheckComponent = () => {
    const [gender, check] = useContext(FormContext)
    const [setSelectBox ,setGender, setCheck] = useContext(ChangeTextContext)
  
  
  
    return (
    <form>
         <input type="radio" name={gender} onChange={() => setGender('男')} /><label htmlFor="gender">男</label>
         <input type="radio" name={gender} onChange={() => setGender('女')} /><label htmlFor="gender">女</label><br />
          <input type="checkbox" name={check} onChange={() => setCheck( 'チェック１')} />チェック１
          <input type="checkbox" name={check} onChange={() => setCheck('チェック２')} />チェック2
          <input type="checkbox" name={check} onChange={() => setCheck( 'チェック３')} />チェック3<br />
         <select name="example" id="output" onChange={(select:any) => setSelectBox(select.value)}>
          <option>サンプル１</option>
          <option>サンプル２</option>
          <option>サンプル３</option>
        </select><br />
    </form>
    )
}

const ButtonCompoent = () => {
    const [putdata] = useContext(ChangeTextContext)
    return(
    <button type="submit"  onClick={putdata}>登録</button>
    )
}

const App = (_props:any) => {
    const [state, setState] = useState('')
   
    const req: VtecxApp.Entry[] = [
        {    
            user:{
                name: _props.name,
                email: _props.email,
                memo: _props.memo,
                gender: _props.gender,
                job: _props.job,
                address: _props.address,
                height:_props.height,
                select: _props.selectBox,
                check: _props.check
            },
            link: [
                {
                    "___href": "/foo/2",
                    "___rel": "self"
                }
            ]
        }
    ]
  
    const putdata = async () => {
        try {
          axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
          const res = await axios.put('/d/foo',req)
          console.log(res)
          console.log(req)
        } catch (e) {
          alert('error')
          console.log(e)
        }
      }

    
  return (
      <div>
          <FormContext.Provider value={[state]}>
              <ChangeTextContext.Provider value={[setState, putdata]}>     
                      <FormComponent />
              </ChangeTextContext.Provider>
          </FormContext.Provider>
      </div>
  )
}
App.defaultProps = {
    name: '',
    email: '',
    memo: '',
    gender: '',
    birthday: '',
    job: '',
    address: '',
    height: '',
    selectBox: '',
    check: ''
}

ReactDOM.render(<App />, document.getElementById('container'))